.Box-one{
  height: 300px;
  width: 400px;
  padding: 20px;
  background-color: rgb(236, 147, 227);
  position: relative;
  animation-name: example-one;
  animation-duration: 20s;
  animation: fade-left 5s ease-in;
  margin-left: 150px;
  border-radius: 10px;
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}
.Box-two{
  height: 300px;
  width: 400px;
  padding: 20px;
  background-color: rgb(133, 188, 240);
  position: relative;
  animation-name: example-two;
  animation-duration: 20s; 
  margin-top: 10px;
  margin: 20px;
  margin-left: 900px;
  animation: fade-right 5s ease-in;
  border-radius: 10px;
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}
.Box-three{
  height: 300px;
  width: 400px;
  padding: 20px;
  background-color: rgb(245, 139, 139);
  position: relative;
  animation-name: example-three;
  animation-duration: 20s;
  margin-top: 10px;
  animation: fade-left 5s ease-in;
  margin-left: 150px;
  border-radius: 10px;
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}
.Box-four{
  height: 300px;
  width: 400px;
  padding: 20px;
  background-color: rgb(206, 236, 122);
  position: relative;
  animation-name: example-four;
  animation-duration: 20s;
  margin-top: 10px;
  margin: 20px;
  margin-left: 900px;
  animation: fade-right 5s ease-in;
  border-radius: 10px;
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}

@keyframes example-one {
  0%   {background-color:rgb(236, 147, 227); left:0px; top:0px;}
  30%  {background-color:rgb(226, 109, 166); left:150px; top:0px;}
}
@keyframes example-two {
  0%   {background-color:rgb(133, 188, 240); left:0px; top:0px;}
  30%  {background-color:rgb(97, 181, 212); right:150px; top:0px;}
}
@keyframes example-three {
  0%   {background-color:rgb(245, 139, 139); left:0px; top:0px;}
  30%  {background-color:rgb(246, 172, 227); left:150px; top:0px;}
}
@keyframes example-four {
  0%   {background-color:rgb(206, 236, 122); left:0px; top:0px;}
  30%  {background-color:rgb(246, 246, 112); right:150px; top:0px;}
}
@keyframes fade-left {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  50% {
    transform: translateX(-50px);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fade-right {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  50% {
    transform: translateX(50px);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

